/* pallete */
:root{
    --primay : #62A87C  ;
    --white  : #F1F2F6;
    --bold : #191919;
    --secondry : #E84855;
    --grad : #32373B;

    --shadow : #ccc;


}

body{
    background-color: var(--white);
    color: var(--bold);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}



.no-link{
    text-decoration: none;
    color : var(--bold);
}


.to-right{
    margin-left: auto !important;
}

.container{
    padding-top: 56px;
}


.inp{
    max-width: 100%;

    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    box-sizing: border-box;
    margin: 8px;
    margin-bottom: 16px;
    padding: 6px 8px;
    border-radius: 4px;
    border: solid 2px var(--primay);
    font-size: 14px;
}

/* navbar */
.navbar{
    position: fixed;

    height: 56px;
    width:100%;
    background-color: var(--white);
    /* border-bottom: 1px solid black; */

    display: flex;
    align-items: center;
    box-shadow: 0 0 15px var(--shadow);

    z-index: 10;
}


.title{
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;

    font-weight: 800;
    color: var(--primay);
    text-transform: uppercase;

    padding: 10px;
}

.btn{
    background-color: var(--primay);
    padding: 8px;
    border-radius: 5px;
    font-size: 18px;
    color: var(--white);
    margin: 8px;
    cursor: pointer;

    text-transform: uppercase;
    font-weight: 600;
}

.yellow{
    background-color: var(--secondry);
}

.username{
    font-size: 18px;
    color: var(--bold);
    text-transform: capitalize;
}


/* page */
.page-controller{
    display: flex;
    align-items: center;
    justify-content: center;
}

.page{
    font-size: 32px;
    
}

/* ============================== */
/* events */

.event-holder{
    display: grid;
    grid-template-columns : repeat(3 , 1fr);
    grid-auto-rows: 400px;
    margin: 16px 15%;
}

.event-image{
    height: 50%;
    overflow: hidden;
    object-fit: cover;
    width: 100%;
}

.event-card{
    margin: 8px;
    box-shadow: 0 0 16px var(--shadow);
    background-color: var(--white);
    border-radius: 8px;
    overflow: hidden;
    /* overflow: hidden; */
    /* padding: 16px; */
}

.event-text{
    padding:0 16px;
    text-overflow: ellipsis;
}

.event-desc{
    font-size: 18px;
    font-weight: 400;
    color: var(--grad);
    line-clamp: 2;
}

/* event page */

.event-container{
    margin: 56px 15%;
    padding-top: 56px;
    padding: 45px;
    box-shadow: 0 0 16px var(--shadow);
    overflow: hidden;

    border-radius: 15px;
}

.event-top{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.event-heading{
    text-transform: capitalize;
    font-size: 32px;
}

.event-desc{
    margin-left: 10px;
    color:var(--grad);
}

.event-form{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.event-commentinp{
    width: 100%;
    background-color: var(--white);
    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;

    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    border-radius: 10px;
    padding: 10px;
    outline: solid 2px var(--grad);
}

.event-comment-submit{
    margin: 10px;
    max-width: fit-content ;
}

.comment-single{
    padding: 1px 10px;
    border-left: 10px solid var(--primay);
    margin-bottom: 8px;
    border-radius: 6px;
}

.comment-comment{
    font-size: 16px;
    color: var(--bold);
}

.bold{
    text-transform: capitalize;
}

.comment-username{
    text-transform: capitalize;
    font-size: 14px;
    color: var(--grad);
}


.loginsignup{
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginsignupform{
    box-shadow: 0 0 15px var(--shadow);
    margin: 32px;
    padding: 32px 8px;
    border-radius: 8px;
    width: 30%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.loginsignupcont{
    display: flex;
    margin-bottom: 16px;
}

.loginsignupcont > *{
    margin: 2px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 4px;
}

.loginsignupheader{
    text-transform: capitalize;
    padding: 0px 8px;
    margin: 0px 8px;
    font-weight: 600;
}

.emptyLine{
    border-bottom: 4px ;
}

.line{
    transition: all 0.1s;
    border-bottom: 4px solid var(--primay);
}